body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  margin-bottom: 10px;
  background-color: transparent;
  background-repeat: no-repeat;
}

.card-odd {
  background-image: linear-gradient(rgba(26, 158, 158, 0.1), rgba(82, 82, 154, 0.1));
}

.card-even {
  background-image: linear-gradient(rgba(82, 82, 154, 0.1), rgba(26, 158, 158, 0.1));
}

img:hover {
  opacity: 0.5; 
}

.cardpoke:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

span.badge {
  margin-right: 5px;
}

.spinner-border {
  width: 3rem;
  height: 3rem;
}

.detail-page {
  background-color: transparent;
  background-image: linear-gradient(rgba(26, 158, 158, 0.1), rgba(82, 82, 154, 0.1));
  background-repeat: no-repeat;
  min-height: 100vh;
}

.btn-catch:hover {
  background-color: #b4e739;
}

.col-centered {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

#inline h5,
#inline p {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  line-height: 28px;
  margin: 0;
  display: inline-block;
  width: 200px;
}
